            </v-card-title> -->
            <v-card-text>
              <span v-html="results"></span>
            </v-card-text>
            <v-card-actions>
              <v-btn
                color="green darken-1"
                text
                @click="dialog = false"
              >
                Fermer
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>


      </v-container>
