<script>

export default {
  name: 'App',

  created() {
    for (let i = 0; i < this.cellTypes.length; i += 1) {
      this.cellTypes[i].counter = 0;
    }
  },

  methods: {
    beep() {
      const snd = new Audio('data:audio/wav;base64,');
      snd.play();
    },
    add(i) {
      if (this.correctMode) {
        if (this.cellTypes[i].counter > 0) {
          this.cellTypes[i].counter -= 1;
          this.counter -= 1;
        }
        this.correctMode = false;
        return;
      }
      if (this.counter >= this.limit) {
        this.beep();
        return;
      }
      this.cellTypes[i].counter += 1;
      this.counter += 1;
      if (this.counter >= this.limit) {
        this.beep();
        this.showResults();
      }
    },
    reset() {
      this.dialogReset = false;
      this.counter = 0;
      for (let i = 0; i < this.cellTypes.length; i += 1) {
        this.cellTypes[i].counter = 0;
      }
    },
    correct() {
      if (this.counter === 0) { return; }
      this.correctMode = !this.correctMode;
    },
    showResults() {
      const table = Array(this.cellTypes.length);
      for (let i = 0; i < this.cellTypes.length; i += 1) {
        table[this.cellTypes[i].order - 1] = this.cellTypes[i];
      }
      this.results = '';
      let currentGroup = null;
      for (let i = 0; i < this.cellTypes.length; i += 1) {
        if (table[i].group !== '' && table[i].group !== currentGroup) {
          currentGroup = table[i].group;
          if (this.results !== '') this.results += '<br/>';
          this.results += `<span class="group">${currentGroup} :</span>`;
        }
        if (this.results !== '') this.results += '<br/>';
        this.results += `${table[i].name} : ${table[i].counter}`;
      }
      this.dialog = true;
    },
  },

